.HeroAction {
  margin-top: 7%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  gap: 2rem;
  width: 100%;
  height: 100%;
  cursor: pointer;
}

@media screen and (min-width: 2000px) {
  .HeroAction {
    justify-content: center;
    gap: 10rem;
  }
}
